.btn
{
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 12px;
    padding: 7px 10px 7px 10px;
    color: $white;
    border: 1px solid white;

    &:hover{

        color: $dark-grey;
        background-color: $white;
        border: 1px solid $dark-grey;

    }

  
};

.btn-primary
{
    background-color: $dark-grey;
    border-radius: 2px;

    &:disabled{

        background-color: $light-grey;
    }
};

.btn-danger
{
    background-color: red;

    transition: background-color .3s ease;

    &:hover{

        background-color: darken($color: red, $amount: 10);
        color: white;
    }
}

.btn-block
{
    width: 100%;
};



////////////////////////////////////--- GOOGLE BUTTON ---////////////////////////////////////////


.google-btn 
{
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper 
    {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }

    .google-icon 
    {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }

    .btn-text 
    {
        float: right;
        margin: 11px 40px 0 0;
        font-size: 14px;
        letter-spacing: 0.2px;
        text-align: center;
    }

    &:hover {
        box-shadow: 0 0 6px $light-grey;
    }

    &:active {
        background: $button-active-blue;
    }
};