
.journal__box-container
{
    display: flex;

}


.journal__sidebar
{
    background-color: $dark-grey;
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
    height: 100vh;
    width: 450px;
}


.journal__sidebar-navbar
{
    display: flex;
    justify-content: space-between;
}


.journal__sidebar-navbar h3
{
   font-weight: lighter;
}


.journal__new-entry
{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s ease;

    &:hover{

        color: $white;
    }
}


.journal__entries
{
    flex: 1 1 auto;
    margin-top: 30px;
    overflow-y: auto;
    
}

.journal__entry
{
    background-color: white;
    border-radius: 4px;
    color: black;
    display: flex;
    justify-content: space-between; 
    margin-bottom: 10px;
    overflow: hidden;

}

.journal__entry-picture
{
    height: 75px;
    width: 75px;
}

.journal__entry-body
{
    padding: 10PX;
}

.journal__entry-title
{
    font-size: 14px;
    font-weight: bold;
}
             
.journal__entry-content
{
    font-size: 10px;
}

.journal__entry-date-box
{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5px;
}

.journal__entry-date-box span
{
    font-size: 12px;
}