
.notes__main-content
{
    display: flex;
    flex-direction: column;
    height: 100%;
}

.notes__content
{
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
}

.notes__title-input, .notes__textarea
{
    border: none;

    &:focus{

        outline: none;
    }
}

.notes__title-input
{
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 10px;
}

.notes__textarea
{
    border: none;
    font-size: 20px;
    flex: 1 1 auto;
    resize: none;
}

.notes__image img
{
    box-shadow: 5px 5px $dark-grey;
    height: 150px;
}

.notes__appbar
{
    align-items: center;
    background-color:$dark-grey;
    color: $white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
}